import {Music} from './music'
import {argmax, min, avg} from '../custom/utils'

const converters = {}
export {converters}


converters.c2 = (mididata)=>{
	
	const mainTrackId = (()=>{
		const trackIds = [...new Set(mididata.notes.map(nt=>nt.trackId))]
		const avgs = trackIds.map(trackId=>avg(mididata.notes.filter(nt=>nt.trackId==trackId).map(nt=>nt.n)))
		return trackIds[argmax(avgs)]
	})()



	const normed = (()=>{
		const interval = (()=>{
			const mainNotes = mididata.notes.filter(nt=>nt.trackId==mainTrackId)

			const t0s = mainNotes.map(nt=>nt.t0)
			const intervals = t0s.map((t,i)=>i?t0s[i]-t0s[i-1]:0)
			
			const counts = new Map()
			intervals.forEach(t=>{
				counts.set(t,(counts.get(t)||0)+1)
			})
			return [...counts.keys()][argmax([...counts.values()])]
		})()

		return normalizeTime(mididata, {
			nperdiv: 100,
			fraction_unit: mididata.timeDivision/interval
		})
	})()

	const data = new Music.MusicData()
	var start_t = min(normed.notes.filter(nt=>nt.trackId==mainTrackId).map(nt=>nt.t0))
	
	;(()=>{
		var mainNotes = normed.notes.filter(nt=>nt.t0<start_t||nt.trackId==mainTrackId)
		var t = -1
		for (let i = 0; i < mainNotes.length; i++) {
			if (Math.floor(mainNotes[i].t0) > Math.floor(t)) {
				t = Math.floor(mainNotes[i].t0)

				const lastKey = data.keyList[data.keyList.length-1]

				if(lastKey) {
					lastKey.t2 = t
				}
				data.keyList.push(new Music.MusicKey({
					t0: t,
					t1: 0,
					t2: mainNotes[i].t1,
					notes: []
				}))
			}
			const lastKey = data.keyList[data.keyList.length-1]
			lastKey.notes.push(mainNotes[i])
		}
	})()

	;(()=>{
		var secondaryNotes = normed.notes.filter(nt=>nt.t0>=start_t&&nt.trackId!=mainTrackId)
		
		if(data.keyList[0].t0>0) {
			data.keyList.unshift(new Music.MusicKey({
				t0: 0,
				t1: 0,
				t2: data.keyList[0].t0,
				notes: []
			}))
		}

		data.keyList.forEach(key=>{
			while(secondaryNotes[0]&&key.t0<=secondaryNotes[0].t0&&secondaryNotes[0].t0<key.t2) {
				key.notes.push(secondaryNotes.shift())
			}
		})
		
		if(!data.keyList[0].notes.length) {
			const start_t = data.keyList[0].t2
			data.keyList.shift()
			data.keyList.forEach(key=>{
				key.t0 -= start_t
				key.t1 -= start_t
				key.t2 -= start_t
				key.notes.forEach(note=>{
					note.t0 -= start_t
					note.t1 -= start_t
				})
			})
		}

	})()

	console.log(data)

	return data
}



const normalizeTime = (mididata, {nperdiv, fraction_unit})=>{
	const result = new Music.MidiData({timeDivision: mididata.timeDivision})
	
	result.notes = mididata.notes.map(note=>{
		const scale = fraction_unit/mididata.timeDivision
		let t0 = note.t0 * scale
		let t1 = note.t1 * scale
		t0 = Math.round(nperdiv*t0)/nperdiv
		
		const nt = {...note}
		nt.t0 = t0
		nt.t1 = t1
		return nt
	})
	return result
}
