import {converters} from './music.converters'
import MidiParser from 'midi-parser-js'
import {max, avg} from '../custom/utils'


export class Music {
	constructor(data) {
		this.data = data
		this.timeKeysMap = []
		this.speed = 1
		this.init()
	}
	
	init() {
		const t_max = max(this.data.keyList.map(noteList=>noteList.t2))
		let i = 0
		for (let t = 0; t <= t_max; t++) {
			if(this.data.keyList[i+1] && t>this.data.keyList[i+1].t0){
				i++
			}
			this.timeKeysMap[t] = i
		}
	}

}

Music.load = async function(file, converterName, params={}) {
	const data = converters[converterName](await Music.getNotesFromFile(file), params)
	
	const music = new Music(data)
	return music
}


Music.MidiData = class MidiData {
	constructor({notes=[], timeDivision=1}={}) {
		this.notes = notes
		this.timeDivision = timeDivision
	}
}

Music.MidiNote = class MidiNote {
	constructor({t0=0,t1=0,n=0,velocity=0,trackId=0}={}) {
		this.t0 = t0
		this.t1 = t1
		this.n = n
		this.velocity = velocity
		this.trackId = trackId
	}
}

Music.MusicData = class MusicData {
	constructor() {
		this.keyList = []
	}
}

Music.MusicKey = class MusicKey {
	constructor({t0=0,t1=0,t2=0,notes=[]}={}) {
		this.t0 = t0
		this.t1 = t1
		this.t2 = t2
		this.notes = notes
	}
}

Music.getNotesFromFile = async function(file) {
	const midiFile = await (await fetch(file)).arrayBuffer()
	const midiObj = MidiParser.parse(new Uint8Array(midiFile))
	
	const timeDivision = midiObj.timeDivision
	const data = new Music.MidiData()
	data.timeDivision = timeDivision
	data.notes = []

	for (let i = 0; i < midiObj.track.length; i++) {
		const track = midiObj.track[i]
		let t = 0
		const attacked = {}
		for (let event of track.event) {
			if(event.metaType)continue;
			t += event.deltaTime
			if(event.type!=8&&event.type!=9)continue;
			if(event.data[1]!=0&&event.type==9) {
				const note = new Music.MidiNote({
					t0: t,
					t1: t,
					n: event.data[0]-20,
					velocity: event.data[1],
					trackId: i
				})
				attacked[event.data[0]] = note
			} else {
				const note = attacked[event.data[0]]
				note.t1 = t
				data.notes.push(note)
			}
		}
	}
	data.notes.sort((a,b)=>{
		return a.t0-b.t0
	})

	console.log(data)
	return data
}


